.box-news {
  margin-top: 20px;
}

@media screen and (min-width: 768px) {
  .box-news {
    flex-wrap: wrap;
    gap: 30px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .box-news {
    flex-wrap: wrap;
    gap: 32px;
    display: flex;
  }
}

.box-news__item {
  height: 675px;
  position: relative;
  overflow: hidden;
}

@media screen and (max-width: 767px) {
  .box-news__item {
    height: 630px;
    margin-bottom: 28px;
  }
}

@media screen and (min-width: 768px) {
  .box-news__item {
    width: calc(50% - 15px);
  }
}

@media screen and (min-width: 1280px) {
  .box-news__item {
    width: calc(33.3333% - 21.3333px);
  }
}

.box-news__section {
  min-width: 112px;
  color: var(--secondary-light-color);
  background: var(--accent-card-btn-bg-color);
  text-align: center;
  border-radius: 0 4px 4px 0;
  padding: 4px 8px;
  font-family: Manrope500, sans-serif;
  font-size: 12px;
  line-height: 1.33;
  display: inline-block;
  position: absolute;
  top: 40px;
  left: 0;
}

@media screen and (min-width: 768px) {
  .box-news__section {
    top: 44px;
  }
}

.box-news___wrap-reading {
  opacity: 0;
  display: none;
}

li.show .box-news___wrap-reading {
  opacity: 1;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  gap: 4px;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

@media screen and (min-width: 768px) {
  li.show .box-news___wrap-reading {
    top: 20px;
    right: 20px;
  }
}

.box-news__reading {
  color: var(--read-color);
  font-family: Manrope700, sans-serif;
  font-size: 14px;
  line-height: 1, 36;
}

.box-news__thumb {
  width: 100%;
  height: 395px;
  background-color: #81bce7;
  border-radius: 4px;
  margin-bottom: 20px;
  overflow: hidden;
}

.box-news__img {
  height: 100%;
  object-fit: cover;
}

.box-news__wrap-title {
  height: 99px;
  margin-bottom: 20px;
  overflow-y: hidden;
}

@media screen and (min-width: 768px) {
  .box-news__wrap-title {
    height: 132px;
  }
}

.box-news__titel {
  letter-spacing: .01em;
  font-family: Manrope700, sans-serif;
  font-size: 24px;
  line-height: 1.38;
}

@media screen and (min-width: 768px) {
  .box-news__titel {
    font-size: 32px;
  }
}

.box-news__wrap-text {
  height: 57px;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .box-news__wrap-text {
    height: 66px;
  }
}

.box-news__text {
  font-family: Manrope400, sans-serif;
  font-size: 14px;
  line-height: 1.36;
}

@media screen and (min-width: 768px) {
  .box-news__text {
    font-style: 16px;
    line-height: 1.38;
  }
}

.box-news__wrap {
  justify-content: space-between;
  display: flex;
}

.box-news__data {
  color: var(--light-text-color);
  font-family: Manrope400, sans-serif;
  font-size: 14px;
  line-height: 1.36;
}

@media screen and (min-width: 768px) {
  .box-news__data {
    font-style: 16px;
    line-height: 1.36;
  }
}

.box-news__link {
  color: var(--accent-primary-color);
  font-family: Manrope400, sans-serif;
  font-size: 14px;
  line-height: 1.36;
}

@media screen and (min-width: 768px) {
  .box-news__link {
    font-style: 16px;
    line-height: 1.36;
  }
}

li.show .overlay {
  pointer-events: none;
  transform: translateX(0);
}

.overlay {
  width: 100%;
  height: 100%;
  background-color: var(--overlay-color);
  transition: transform var(--timing-function);
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.box-news__favorite-btn {
  min-width: 126px;
  height: 28px;
  background: var(--secondary-light-color);
  border: none;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  display: flex;
  position: absolute;
  top: 355px;
  right: 12px;
}

.box-news__favorite-btn.favorite .box-news__favorite-p {
  padding-left: 6px;
}

.box-news__favorite-btn.favorite .box-news__favorite-svg {
  fill: var(--accent-third-color);
  padding-right: 6px;
}

.box-news__favorite-p {
  text-align: center;
  font-family: Manrope500, sans-serif;
  font-size: 12px;
  line-height: 1.33;
}

.box-news__favorite-svg {
  width: auto;
  height: auto;
  fill: none;
}

body.dark-theme .box-news__favorite-p {
  color: var(--primary-dark-color);
}

